.card-card {
  position: absolute;
  top: 0;
  width: 100px;
  height: 200px;
  background-color: white;
  border-radius: 8px;
  border: solid 1px black;
  padding: 3px 3px 3px 3px;
  display: flex;
  align-items: flex-start;
  transition: top 1s;
}

.card-selected {
  top: -30px;
}

.card-hidden {
  display: none;
}

.card-value {
  font-size: 20px;
}

.card-red {
  color: red;
}

.card-black {
  color: black;
}